import React from "react";

const initState = {
    autor: ['lej'],
    tytu: []
}

const booksReducer = (state = initState, action) => {
    switch(action.type) {
        case "FETCH_BOOKS":
            return {
                ...state,
                autor: action.payload.author,
                tytu: action.payload.tytu
            }
        case "CHANGE_AUTHOR":
            return {
                ...state,
                autor: action.payload.author
            }
        case "CHANGE_TITLE":
            return {
                ...state,
                tytu: action.payload.tytu
            }
        default: 
            return {...state}
    }
}

export default booksReducer